import React, { useState } from 'react';
import { Grid, ClickAwayListener } from "@material-ui/core";
import Popper from '@material-ui/core/Popper';
import Style from './HeaderStyle';
import { makeStyles } from "@material-ui/core/styles";

const useStyles = makeStyles(Style);
const BubbleShape = (props) => {
    const classes = useStyles();


    return (
        <Grid container>
        <ClickAwayListener onClickAway={props.close}>
            <Grid item xs={12} className={classes.MainGrid}>
                <div className={props.Class}>
                    <div>{props.message}</div>
                    <Popper open={props.open}>
                    </Popper>
                </div>
            </Grid>
            </ClickAwayListener>
        </Grid>
    )
}

export default BubbleShape;
