import React, { useState } from 'react';
import { Grid, Button } from '@material-ui/core';
import Style from './FooterStyle';
import { makeStyles } from "@material-ui/core/styles";
import RefreshIcon from '@material-ui/icons/Refresh';
import ReplayIcon from '@material-ui/icons/Replay';
import RemoveIcon from '@material-ui/icons/Remove';
import AddIcon from '@material-ui/icons/Add';


const useStyles = makeStyles(Style);
const Footer = () => {
    const classes = useStyles();
    const [count, setCount] = useState(100);

    /*
     * Count Increase
    */
    const handleCount = () => {
        setCount(count - 10)
        
        if(count < 10){
            setCount(0);
        }
    }

    /*
     * Count Decrease
    */
    const handleAddCount = () => {
        setCount(count + 10)
    }

    const handleUndo = () => {
        window.Module.inputReceiver().undo();
        window.drawCanvas();
    }

    const handleRedo = () => {
        window.Module.inputReceiver().redo();
        window.drawCanvas();
    }

    return (
        <Grid container className="footer">
            <Grid item xs={12} className={classes.footer}>
                <div className={`${classes.backClass} undo_redo_section`}>
                    <Button className="resButton"><ReplayIcon className={classes.iconClass} onClick={() => handleUndo()}/></Button>
                    <Button className="resButton"><RefreshIcon className={classes.iconClass} onClick={() => handleRedo()}/> </Button>
                </div>
                <div className={`${classes.countClass} counter_percent`}>
                    <Button className={classes.countBtn}><RemoveIcon className={classes.iconClass} onClick={() => handleCount()} /></Button>
                    <div className={classes.valueClass}> {count}% </div> 
                    <Button className={classes.countBtn}><AddIcon className={classes.iconClass} onClick={() => handleAddCount()} /></Button>
                </div>
            </Grid>
        </Grid>
    )
}

export default Footer;